import "./Step1.scss";
import { Button } from "../../components/Button/Button";
import { Input } from "../../components/Input/Input";
import { Select } from "../../components/Select/Select";
import { Sex } from "../../utils/enums";
import { Link, useNavigate } from "react-router-dom";
import { Formik, Form, Field } from "formik";
import stepper_1 from "../../images/stepper_1.svg";

import * as Yup from "yup";

export default function Step1() {
  const nicknameRegExp = /^[A-z-0-9]*$/;
  const nameSurnameRegExp = /^[A-z-]+$/;

  const navigateNextPage = useNavigate();

  return (
    <div className="step1__container">
      <img src={stepper_1} alt="step_1" />
      <Formik
        initialValues={{
          nickname: "",
          name: "",
          surname: "",
          sex: "",
        }}
        validationSchema={Yup.object({
          nickname: Yup.string()
            .max(30, "  30 ")
            .matches(nicknameRegExp, "     ")
            .required("    "),
          name: Yup.string()
            .max(50, "  50 ")
            .matches(nameSurnameRegExp, "   ")
            .required("    "),
          surname: Yup.string()
            .max(50, "  50 ")
            .matches(nameSurnameRegExp, "   ")
            .required("    "),
          sex: Yup.string()
            .oneOf(["", ""], "Invalid Job Type")
            .required("Required"),
        })}
        onSubmit={async (values, { setSubmitting }) => {
          await new Promise((r) => setTimeout(r, 500));
          setSubmitting(false);
          console.log(JSON.stringify(values, null, 2));
          navigateNextPage("/step2");
        }}
      >
        <Form>
          <div className="user-input">
            <Input
              label="Nickname"
              id="nickname"
              name="nickname"
              type="text"
              placeholder=" nickname "
            />
          </div>
          <div className="user-input">
            <Input
              label=""
              id="name"
              name="name"
              type="text"
              placeholder=" "
            />
          </div>
          <div className="user-input">
            <Input
              label=""
              id="surname"
              name="surname"
              type="text"
              placeholder=" "
            />
          </div>
          <div className="user-input">
            <Select label="" id="sex" name="sex">
              <option value=""> </option>
              <option value={Sex.MAN}></option>
              <option value={Sex.WOMAN}></option>
            </Select>
          </div>
          <div className="navigation-buttons">
            <Link to="/">
              <Button></Button>
            </Link>
            <Button></Button>
          </div>
        </Form>
      </Formik>
    </div>
  );
}
